@charset "UTF-8";
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter_24pt-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter_24pt-Medium.ttf") format("truetype");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter_24pt-SemiBold.ttf") format("truetype");
  font-style: normal;
  font-weight: 600;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter_24pt-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter_24pt-ExtraBold.ttf") format("truetype");
  font-style: normal;
  font-weight: 800;
}
@font-face {
  font-family: "Inter";
  src: url("../fonts/Inter_24pt-Black.ttf") format("truetype");
  font-style: normal;
  font-weight: 900;
}
html {
  box-sizing: border-box;
  position: relative;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-style: normal;
  background: linear-gradient(to bottom, rgba(205, 6, 255, 0.15) 0%, transparent 100%), linear-gradient(135deg, #1b1a1b 0%, #2a1b2a 50%, #1b1a1b 100%);
  color: #FFFFFF;
  font-family: Arial, sans-serif;
  font-size: 20px;
  min-height: 100vh;
  margin: 0 auto;
}

img, svg {
  max-width: 100%;
  max-height: 100%;
  height: auto;
}

button {
  cursor: pointer;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

figure, fieldset {
  margin: 0;
  border: none;
  padding: 0;
}

section {
  padding: 50px 0 50px 0;
}

h1, .h1 {
  font-size: 45px;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
}

h2, .h2 {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-style: normal;
  text-align: center;
  text-transform: uppercase;
  font-size: 40px;
  line-height: 50px;
  position: relative;
  z-index: 2;
  margin: 0 0 80px 0;
  animation: titleAppear 1s ease-out forwards;
}
h2::after, .h2::after {
  content: "";
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: linear-gradient(45deg, #CD06FF, #FF06CD);
  border-radius: 2px;
  animation: lineExpand 1s ease-out 0.5s forwards;
  scale: 0 1;
}

p {
  font-size: 20px;
}

.container {
  max-width: 1180px;
  margin: 0 auto;
}

.hidden {
  display: none;
}

@media screen and (max-width: 1320px) {
  section {
    padding: 35px 60px 35px 60px;
  }
  .container {
    max-width: 942px;
    padding: 0 20px;
  }
  h1, .h1 {
    font-size: 35px;
  }
  h2, .h2 {
    font-size: 30px;
    line-height: 40px;
    margin: 0 0 50px 0;
  }
  p {
    font-size: 18px;
  }
  circle {
    stroke: #6C0287;
    stroke-width: 1;
  }
}
@media screen and (max-width: 1023px) {
  section {
    padding: 35px 40px 35px 40px;
  }
  .container {
    max-width: 700px;
    padding: 0 15px;
  }
  h1, .h1 {
    font-size: 28px;
  }
  h2, .h2 {
    font-size: 24px;
    line-height: 36px;
  }
  p {
    font-size: 16px;
  }
}
@media screen and (max-width: 767px) {
  h1, .h1 {
    font-size: 25px;
  }
  h2, .h2 {
    font-size: 18px;
    line-height: 20px;
    margin: 0 0 40px 0;
  }
  .container {
    max-width: 480px;
  }
  p {
    font-size: 14px;
  }
  section {
    padding: 20px 15px 20px 15px;
  }
}
