.slider__track {
  display: flex;
  transition: transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.slider__slide {
  min-width: 100%;
  flex-shrink: 0;
}

.slider__btn {
  border: 2px solid rgba(205, 6, 255, 0.3);
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}
.slider__btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, #CD06FF, #FF06CD);
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 50%;
}
.slider__btn:hover {
  border-color: #CD06FF;
  color: #ffffff;
  transform: scale(1.1);
}
.slider__btn:hover::before {
  opacity: 0.2;
}
.slider__btn:active {
  transform: scale(0.95);
}
.slider__btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
  transform: scale(1);
}
.slider__btn:disabled:hover {
  border-color: rgba(205, 6, 255, 0.3);
  color: rgba(255, 255, 255, 0.7);
  transform: scale(1);
}
.slider__btn:disabled:hover::before {
  opacity: 0;
}
.slider__btn:focus {
  outline: 2px solid rgba(205, 6, 255, 0.5);
  outline-offset: 2px;
}
.slider__icon {
  position: relative;
  z-index: 1;
  transition: transform 0.3s ease;
}

.slider__dots {
  display: flex;
  gap: 12px;
  align-items: center;
  justify-content: center;
}

.slider__dot {
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  border: none;
}
.slider__dot::before {
  content: "";
  position: absolute;
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
  border: 2px solid transparent;
  border-radius: 50%;
  transition: border-color 0.3s ease;
}
.slider__dot:hover {
  background: rgba(255, 255, 255, 0.6);
  transform: scale(1.2);
}
.slider__dot:focus {
  outline: 2px solid rgba(205, 6, 255, 0.5);
  outline-offset: 2px;
}
.slider__dot--active {
  background: linear-gradient(45deg, #CD06FF, #FF06CD);
  transform: scale(1.3);
  box-shadow: 0 0 15px rgba(205, 6, 255, 0.6);
}
.slider__dot--active::before {
  border-color: rgba(205, 6, 255, 0.3);
}

@media (hover: none) and (pointer: coarse) {
  .slider__btn {
    min-width: 44px;
    min-height: 44px;
  }
  .slider__btn:hover {
    transform: none;
    border-color: rgba(205, 6, 255, 0.3);
    color: rgba(255, 255, 255, 0.7);
  }
  .slider__btn:hover::before {
    opacity: 0;
  }
  .slider__dot {
    min-width: 32px;
    min-height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent !important;
    box-shadow: none !important;
  }
  .slider__dot::after {
    content: "";
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.3);
    transition: all 0.3s ease;
  }
  .slider__dot--active::after {
    background: linear-gradient(45deg, #CD06FF, #FF06CD);
    box-shadow: 0 0 15px rgba(205, 6, 255, 0.6);
  }
  .slider__dot:hover {
    transform: none;
  }
  .slider__dot:hover::after {
    background: rgba(255, 255, 255, 0.6);
  }
  .slider__dot::before {
    display: none;
  }
}
@media screen and (max-width: 767px) {
  .slider__slide {
    flex: 0 0 100%;
    max-width: 100%;
    box-sizing: border-box;
  }
  .slider__btn {
    width: 44px;
    height: 44px;
  }
  .slider__icon {
    width: 16px;
    height: 16px;
  }
  .slider__dots {
    gap: 8px;
  }
  .slider__dot {
    width: 8px;
    height: 8px;
  }
  .slider__dot::before {
    top: -3px;
    left: -3px;
    right: -3px;
    bottom: -3px;
    border-width: 1px;
  }
  .slider__dot::after {
    width: 8px;
    height: 8px;
  }
}
@media screen and (max-width: 480px) {
  .slider__btn {
    width: 40px;
    height: 40px;
  }
  .slider__icon {
    width: 14px;
    height: 14px;
  }
  .slider__dots {
    gap: 6px;
  }
  .slider__dot {
    width: 6px;
    height: 6px;
  }
  .slider__dot::after {
    width: 6px;
    height: 6px;
  }
}
@media screen and (max-width: 360px) {
  .slider__btn {
    width: 36px;
    height: 36px;
  }
  .slider__icon {
    width: 12px;
    height: 12px;
  }
  .slider__dots {
    gap: 4px;
  }
  .slider__dot {
    width: 5px;
    height: 5px;
  }
  .slider__dot::after {
    width: 5px;
    height: 5px;
  }
}
