  position: relative;
  z-index: 2;
}

.about-us__gallery {
  grid-column: 1/2;
  grid-row: 1/3;
  display: grid;
  grid-template-columns: repeat(13, 1fr);
  grid-template-rows: repeat(3, auto);
  gap: 10px;
  opacity: 0;
  animation: galleryAppear 1s ease-out 0.3s forwards;
}

.about-us__gallery-item {
  position: relative;
  border-radius: 15px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}
.about-us__gallery-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(205, 6, 255, 0.2) 0%, rgba(255, 6, 205, 0.2) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 2;
}
.about-us__gallery-item::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s ease;
  z-index: 3;
  font-size: 20px;
  color: #1B1A1B;
}
.about-us__gallery-item:hover {
  transform: translateY(-10px) scale(1.05);
  box-shadow: 0 20px 40px rgba(205, 6, 255, 0.4);
}
.about-us__gallery-item:hover::before {
  opacity: 1;
}
.about-us__gallery-item:hover::after {
  opacity: 1;
  content: "";
}
.about-us__gallery-item:hover .about-us__image {
  transform: scale(1.1);
  filter: brightness(1.2);
}
.about-us__gallery-item:nth-child(1) {
  animation: itemFloat 1s ease-out 0.1s forwards;
  transform: translateY(50px);
}
.about-us__gallery-item:nth-child(2) {
  animation: itemFloat 1s ease-out 0.2s forwards;
  transform: translateY(50px);
}
.about-us__gallery-item:nth-child(3) {
  animation: itemFloat 1s ease-out 0.3s forwards;
  transform: translateY(50px);
}
.about-us__gallery-item:nth-child(4) {
  animation: itemFloat 1s ease-out 0.4s forwards;
  transform: translateY(50px);
}
.about-us__gallery-item:nth-child(5) {
  animation: itemFloat 1s ease-out 0.5s forwards;
  transform: translateY(50px);
}
.about-us__gallery-item:nth-child(6) {
  animation: itemFloat 1s ease-out 0.6s forwards;
  transform: translateY(50px);
}
.about-us__gallery-item:nth-child(7) {
  animation: itemFloat 1s ease-out 0.7s forwards;
  transform: translateY(50px);
}
.about-us__gallery-item--vr {
  grid-row: 1/2;
  grid-column: 1/7;
}
.about-us__gallery-item--games {
  grid-row: 1/2;
  grid-column: 7/13;
}
.about-us__gallery-item--fifa {
  grid-row: 2/3;
  grid-column: 1/4;
}
.about-us__gallery-item--pad {
  grid-row: 2/3;
  grid-column: 4/8;
}
.about-us__gallery-item--controller {
  grid-row: 2/3;
  grid-column: 8/13;
}
.about-us__gallery-item--karaoke {
  grid-row: 3/4;
  grid-column: 1/9;
}
.about-us__gallery-item--vr2 {
  grid-row: 3/4;
  grid-column: 9/13;
}

.about-us__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.5s ease;
}

.about-us__text {
  grid-column: 2/3;
  grid-row: 1/2;
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 40px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  opacity: 0;
  animation: textSlideIn 1s ease-out 0.6s forwards;
}

.about-us__description {
  font-size: 18px;
  line-height: 28px;
  margin: 0 0 20px 0;
  color: rgba(255, 255, 255, 0.9);
  transition: color 0.3s ease;
}
.about-us__description:last-child {
  margin: 0;
}
.about-us__description:hover {
  color: #ffffff;
}

.about-us__quote {
  grid-column: 2/3;
  grid-row: 2/3;
  background: linear-gradient(135deg, rgba(205, 6, 255, 0.1) 0%, rgba(255, 6, 205, 0.1) 100%);
  backdrop-filter: blur(15px);
  border-radius: 25px;
  padding: 40px;
  border: 2px solid rgba(205, 6, 255, 0.2);
  position: relative;
  opacity: 0;
  animation: quoteAppear 1s ease-out 0.9s forwards;
}
.about-us__quote::before {
  content: '"';
  position: absolute;
  top: -20px;
  left: 30px;
  font-size: 80px;
  color: rgba(205, 6, 255, 0.3);
  font-family: serif;
  line-height: 1;
}

.about-us__quote-text {
  font-size: 20px;
  line-height: 30px;
  margin: 0 0 30px 0;
  font-style: italic;
  color: #ffffff;
}

.about-us__quote-highlight {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-style: normal;
  color: var(--neon-purple, #CD06FF);
  text-transform: uppercase;
  text-shadow: 0 0 10px rgba(205, 6, 255, 0.5);
}

.about-us__cite {
  display: flex;
  align-items: center;
  gap: 15px;
  font-style: normal;
}

.about-us__author-photo {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid var(--neon-purple, #CD06FF);
  box-shadow: 0 0 20px rgba(205, 6, 255, 0.3);
}

.about-us__author {
  font-size: 16px;
  color: rgba(255, 255, 255, 0.8);
  line-height: 22px;
}

@keyframes patternMove {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(4px);
  }
}
@keyframes galleryAppear {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes itemFloat {
  from {
    transform: translateY(50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes textSlideIn {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes quoteAppear {
  from {
    opacity: 0;
    transform: translateY(30px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
