.booking {
  padding: 80px 0;
}

.booking__form {
  max-width: 1200px;
  margin: 0 auto;
}

.booking__fieldset {
  border: none;
  margin: 0 0 40px 0;
  padding: 0;
}
.booking__fieldset--halls {
  margin-bottom: 60px;
}
.booking__fieldset--datetime {
  margin-top: 40px;
}

.booking__legend {
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-style: normal;
  font-size: 32px;
  color: #ffffff;
  margin-bottom: 30px;
  text-align: center;
  width: 100%;
}

.booking__halls {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.booking__hall-label {
  position: relative;
  cursor: pointer;
  display: block;
  height: 200px;
  border-radius: 15px;
  overflow: hidden;
  transition: all 0.3s ease;
}
.booking__hall-label:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(205, 6, 255, 0.3);
}

.booking__hall-input {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}
.booking__hall-input:checked + .booking__hall-visual {
  border: 3px solid #CD06FF;
  box-shadow: 0 0 30px rgba(205, 6, 255, 0.6);
  transform: scale(1.02);
}
.booking__hall-input:checked + .booking__hall-visual::after {
  opacity: 1;
}
.booking__hall-input--80s-vibes + .booking__hall-visual {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/rooms/80s-vibes.jpg");
  background-size: cover;
  background-position: center;
}
.booking__hall-input--star-wars + .booking__hall-visual {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/rooms/star-wars.jpg");
  background-size: cover;
  background-position: center;
}
.booking__hall-input--wild-west + .booking__hall-visual {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/rooms/wild-west.jpg");
  background-size: cover;
  background-position: center;
}
.booking__hall-input--neon-style + .booking__hall-visual {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/rooms/neon-style.jpg");
  background-size: cover;
  background-position: center;
}

.booking__hall-visual {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-style: normal;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  border: 2px solid transparent;
  border-radius: 15px;
  font-size: 24px;
  color: #ffffff;
  text-transform: uppercase;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  transition: all 0.3s ease;
  position: relative;
