.entertainment__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px 20px;
  align-items: flex-end;
  position: relative;
  z-index: 2;
}

.entertainment__item {
  display: flex;
  position: relative;
  width: 380px;
  height: 228px;
  flex-basis: 380px;
  border-radius: 20px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0.02) 100%);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  opacity: 0;
  transform: translateY(50px);
}
.entertainment__item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(205, 6, 255, 0.1) 0%, rgba(108, 2, 135, 0.1) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 1;
}
.entertainment__item::after {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.1) 50%, transparent 70%);
  transform: rotate(45deg);
  opacity: 0;
  transition: all 0.6s ease;
  z-index: 3;
}
.entertainment__item:hover {
  transform: translateY(-15px) scale(1.05);
  box-shadow: 0 25px 50px rgba(205, 6, 255, 0.2), 0 15px 30px rgba(108, 2, 135, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  border-color: rgba(205, 6, 255, 0.3);
}
.entertainment__item:hover::before {
  opacity: 1;
}
.entertainment__item:hover::after {
  top: -100%;
  left: -100%;
  opacity: 1;
}
.entertainment__item:hover .entertainment__image {
  transform: scale(1.1);
  filter: brightness(1.2) saturate(1.3);
}
.entertainment__item:hover .entertainment__description {
  text-shadow: 0 0 20px rgba(205, 6, 255, 0.8);
