.main-banner {
  position: relative;
}
.main-banner::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  animation: backgroundPulse 8s ease-in-out infinite;
}

.main-banner__container {
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 2;
}

.main-banner__text-wrap {
  position: relative;
  height: 823px;
  max-width: 1180px;
  margin: 0 auto;
  flex-basis: 1174px;
  padding-top: 100px;
}
.main-banner__text-wrap:after {
  content: "";
  background-image: url("../img/main-banner.png");
  background-size: 675px 762px;
  width: 675px;
  height: 762px;
  display: inline-block;
  position: absolute;
  bottom: -62px;
  left: 500px;
  opacity: 0.9;
  animation: floatImage 6s ease-in-out infinite;
  mask-image: linear-gradient(to bottom, black 70%, transparent 100%);
}

.main-banner__subtitle {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  font-style: normal;
  text-transform: uppercase;
  font-size: 150px;
  letter-spacing: -15px;
  line-height: 100%;
  margin: 0;
  padding: 0;
  background: linear-gradient(45deg, #CD06FF, #FF06CD);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradientShift 3s ease-in-out infinite;
  text-shadow: 0 0 50px rgba(205, 6, 255, 0.5);
}

.main-banner__title {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 28px;
  padding: 0 0 0 100px;
  line-height: 50px;
  margin: 0 0 30px 0;
  text-align: left;
  text-transform: unset;
  opacity: 0;
  animation: slideInLeft 1s ease-out 0.5s forwards;
}

.main-banner__advantages {
  display: flex;
  flex-direction: row;
  gap: 40px;
  margin: 0 0 150px 0;
  opacity: 0;
  animation: slideInUp 1s ease-out 1s forwards;
}

.main-banner__advantage {
  display: flex;
