  align-items: center;
  justify-content: flex-end;
  flex-basis: 210px;
  transition: transform 0.3s ease;
}
.main-banner__advantage:hover {
  transform: translateY(-10px);
}

.main-banner__advantage-number {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  font-style: normal;
  margin: 0;
  padding: 0;
