  color: var(--neon-purple, #CD06FF);
  font-variant: small-caps;
  text-shadow: 0 0 30px rgba(205, 6, 255, 0.8);
  animation: numberPulse 2s ease-in-out infinite;
}
.main-banner__advantage-number::after {
  content: "";
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 3px;
  background: linear-gradient(45deg, #CD06FF, #FF06CD);
  transition: width 0.3s ease;
}
.main-banner__advantage-number:hover::after {
  width: 100%;
}

.main-banner__advantage-text {
  margin: 0;
  padding: 0;
  transition: color 0.3s ease;
}
.main-banner__advantage:hover .main-banner__advantage-text {
  color: #CD06FF;
}

.main-banner__link {
  display: inline-block;
  opacity: 0;
  animation: slideInUp 1s ease-out 1.5s forwards;
}

.main-banner__button {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-style: normal;
  background: linear-gradient(45deg, #6C0287, #640AA3);
  border: none;
  border-radius: 50px;
  width: 374px;
  height: 80px;
  box-shadow: 0 0 30px rgba(205, 6, 255, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.1);
  font-size: 26px;
  line-height: 30px;
  text-transform: uppercase;
  color: #FFFFFF;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}
.main-banner__button::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.6s ease;
}
.main-banner__button:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(205, 6, 255, 0.6), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.main-banner__button:hover::before {
  left: 100%;
}
.main-banner__button:focus {
  outline: 2px solid #CD06FF;
}
.main-banner__button:active {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(205, 6, 255, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.main-banner__scroll {
  display: inline-block;
  position: absolute;
  background-color: transparent;
  border: none;
  top: 646px;
  left: 536px;
  z-index: 99;
  opacity: 0;
  animation: fadeInBounce 1s ease-out 2s forwards;
}

.main-banner__scroll-icon {
  border: 2px solid rgba(255, 255, 255, 0.6);
  border-radius: 50%;
  transition: all 0.4s ease;
  animation: scrollPulse 2s ease-in-out infinite;
}
.main-banner__scroll-icon:hover {
  border-color: #CD06FF;
  background: rgba(205, 6, 255, 0.1);
  transform: scale(1.1);
}
.main-banner__scroll-icon:hover .main-banner__scroll-rect {
  fill: #CD06FF;
}
.main-banner__scroll-icon:active {
  background: #CD06FF;
  border-color: #CD06FF;
  transform: scale(0.95);
}
.main-banner__scroll-icon:active .main-banner__scroll-rect {
  fill: #FFFFFF;
}

.main-banner__scroll-circle {
  fill: transparent;
}

.main-banner__scroll-rect {
  fill: rgba(255, 255, 255, 0.8);
  transition: fill 0.3s ease;
}

@keyframes backgroundPulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}
@keyframes gradientShift {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}
@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeInBounce {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  60% {
    opacity: 1;
    transform: translateY(-5px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes floatImage {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
  }
  50% {
    transform: translateY(-20px) rotate(1deg);
  }
}
@keyframes numberPulse {
  0%, 100% {
    text-shadow: 0 0 30px rgba(205, 6, 255, 0.8);
    transform: scale(1);
  }
  50% {
    text-shadow: 0 0 40px #cd06ff;
    transform: scale(1.02);
  }
}
@keyframes scrollPulse {
  0%, 100% {
    opacity: 0.8;
    transform: translateY(0);
  }
  50% {
    opacity: 1;
    transform: translateY(-5px);
  }
}
