  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.3) 0%, rgba(108, 2, 135, 0.2) 100%);
  z-index: 0;
  transition: opacity 0.3s ease;
}
.room__item:hover {
  transform: scale(1.05);
  box-shadow: 0 20px 40px rgba(108, 2, 135, 0.4);
}
.room__item:hover::before {
  opacity: 0.7;
}
.room__item:hover .room__image {
  transform: scale(1.1);
}
.room__item:hover .room__description {
  transform: scale(1.05);
  text-shadow: 0 0 20px rgba(205, 6, 255, 0.8);
}

.room__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  transition: transform 0.4s ease;
}

.room__description {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-style: normal;
  position: relative;
  text-transform: uppercase;
  font-size: 70px;
  white-space: pre-line;
  text-align: center;
  line-height: 80px;
  margin: 0;
  z-index: 1;
  color: #ffffff;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
  transition: all 0.3s ease;
}

.room__slider {
  display: none;
  position: relative;
  max-width: 100%;
  margin: 0 auto;
  overflow: hidden;
}

.room__slider-wrapper {
  overflow: hidden;
  border-radius: 15px;
}

.room__slider-slide {
  position: relative;
}

.room__slider-item {
  position: relative;
  width: 100%;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  overflow: hidden;
}
.room__slider-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.4) 0%, rgba(108, 2, 135, 0.3) 100%);
  z-index: 0;
}

.room__slider-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.room__slider-description {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-style: normal;
  position: relative;
  text-transform: uppercase;
  font-size: 32px;
  line-height: 40px;
  text-align: center;
  margin: 0;
  z-index: 1;
  color: #ffffff;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
}

.room__controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 30px;
  margin-top: 30px;
  padding-bottom: 10px;
}

.room__btn {
  width: 50px;
  height: 50px;
}

.room__icon {
  width: 20px;
  height: 20px;
}

.room__dots {
  gap: 12px;
}

.room__dot {
  width: 10px;
  height: 10px;
}

@media screen and (max-width: 1320px) {
  .room {
    padding: 35px 0 70px 0;
  }
  .room__list {
    gap: 18px;
  }
  .room__item {
    width: 441px;
    height: 267px;
    flex-basis: 441px;
    border-radius: 12px;
  }
  .room__item:hover {
    transform: scale(1.03);
  }
  .room__description {
    font-size: 55px;
    line-height: 65px;
  }
}
@media screen and (max-width: 1023px) {
  .room {
    padding: 40px 0 60px 0;
  }
  .room__item {
    width: 325px;
    height: 200px;
    flex-basis: 325px;
    border-radius: 10px;
  }
  .room__item:hover {
    transform: scale(1.02);
    box-shadow: 0 15px 30px rgba(108, 2, 135, 0.3);
  }
  .room__description {
    font-size: 40px;
    line-height: 50px;
  }
}
@media screen and (max-width: 767px) {
  .room {
    padding: 30px 0 50px 0;
  }
  .room__list {
    display: none;
  }
  .room__slider {
    display: block;
  }
  .room__slider-item {
    height: 220px;
    border-radius: 12px;
  }
  .room__slider-description {
    font-size: 28px;
    line-height: 36px;
  }
  .room__controls {
    gap: 20px;
    margin-top: 25px;
  }
  .room__dots {
    gap: 8px;
  }
  .room__dot {
    width: 8px;
    height: 8px;
  }
}
@media screen and (max-width: 480px) {
  .room {
    padding: 25px 0 40px 0;
  }
  .room__slider-item {
    height: 180px;
    border-radius: 10px;
  }
  .room__slider-description {
    font-size: 24px;
    line-height: 30px;
  }
  .room__controls {
    gap: 15px;
    margin-top: 20px;
  }
  .room__dots {
    gap: 6px;
  }
  .room__dot {
    width: 6px;
    height: 6px;
  }
}
@media (hover: none) and (pointer: coarse) {
  .room__item:hover {
    transform: none;
    box-shadow: none;
  }
  .room__item:hover::before {
    opacity: 0.3;
  }
  .room__item:hover .room__image {
    transform: none;
  }
  .room__item:hover .room__description {
    transform: none;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
  }
  .room__dot::after {
    width: 10px;
    height: 10px;
  }
}
@media screen and (hover: none) and (pointer: coarse) and (max-width: 767px) {
  .room__dot::after {
    width: 8px;
    height: 8px;
  }
}
@media screen and (hover: none) and (pointer: coarse) and (max-width: 480px) {
  .room__dot::after {
    width: 6px;
    height: 6px;
  }
}
@media screen and (hover: none) and (pointer: coarse) and (max-width: 360px) {
  .room__dot::after {
    width: 5px;
    height: 5px;
  }
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
.modal--active {
  opacity: 1;
  visibility: visible;
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  cursor: pointer;
}

.modal__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #1B1A1B;
  border: 2px solid #CD06FF;
  border-radius: 15px;
  width: 90%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  padding: 40px;
}

.modal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  background: transparent;
  border: none;
  color: #FFFFFF;
  cursor: pointer;
  padding: 5px;
  transition: color 0.3s ease;
}
.modal__close:hover {
  color: #CD06FF;
}

.modal__header {
  margin-bottom: 30px;
  text-align: center;
}

.modal__title {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 28px;
  color: #FFFFFF;
  text-transform: uppercase;
}

.modal__form-group {
  margin-bottom: 20px;
}

.modal__input {
  width: 100%;
  height: 50px;
  background-color: transparent;
  border: 2px solid #CD06FF;
  border-radius: 8px;
  padding: 15px;
  color: #FFFFFF;
  font-size: 16px;
  transition: border-color 0.3s ease;
}
.modal__input::placeholder {
  color: rgba(255, 255, 255, 0.6);
}
.modal__input:focus {
  outline: none;
  border-color: #6C0287;
}

.modal__textarea {
  width: 100%;
  background-color: transparent;
  border: 2px solid #CD06FF;
  border-radius: 8px;
  padding: 15px;
  color: #FFFFFF;
  font-size: 16px;
  resize: vertical;
  font-family: inherit;
  transition: border-color 0.3s ease;
}
.modal__textarea::placeholder {
  color: rgba(255, 255, 255, 0.6);
}
.modal__textarea:focus {
  outline: none;
  border-color: #6C0287;
}

.modal__submit {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  font-style: normal;
  width: 100%;
  height: 55px;
  background-color: transparent;
  border: 3px solid #CD06FF;
  border-radius: 10px;
  color: #FFFFFF;
  font-size: 18px;
  text-transform: uppercase;
  cursor: pointer;
  transition: background-color 0.4s ease, border-color 0.4s ease;
}
.modal__submit:hover {
  background-color: #6C0287;
  border-color: #6C0287;
}
.modal__submit:focus {
  outline: none;
  background-color: #6C0287;
  border-color: #6C0287;
}
.modal__submit:active {
  background-color: #640AA3;
  border-color: #640AA3;
}

@media screen and (max-width: 767px) {
  .modal__content {
    padding: 30px 20px;
    width: 95%;
  }
  .modal__title {
    font-size: 24px;
  }
  .modal__input,
  .modal__textarea {
    font-size: 14px;
  }
  .modal__submit {
    font-size: 16px;
    height: 50px;
  }
}